import React from 'react';
import ReactDom from 'react-dom';

import { Form, FormItem, Label, Input, Textarea } from '../index';
import { Demo, DemoPanel } from '../../../external/demo/index';
import Doc from './doc.md';

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  mustBeTwo(val, callback) {
    callback({
      isValid: val === '2',
      msgs: ['做人一定要2']
    });
  }

  render() {
    return (
      <div>
        <Demo doc={<Doc />} name="Form">
          <DemoPanel title="Form.Input默认开启校验">
            <Input
              type="text"
              placeholder="请填写数字"
              patterns="inside|range"
              tips="只能是1,2,3|超出范围"
              validator={this.mustBeTwo}
              data-inside={[1, 2, 3]}
              data-range={{ min: 1, max: 9 }}
            />
          </DemoPanel>

          <DemoPanel title="Form.Input关闭校验">
            <Input
              type="text"
              placeholder="关闭校验"
              isChecking={false}
            />
          </DemoPanel>

          <DemoPanel title="Form.Textarea默认开启校验">
            <Textarea
              placeholder="请填写3-7个字"
              defaultValue="哈撒企"
              patterns="not|limit"
              tips="不能为空|应在3到7个字内"
              data-not={''}
              data-limit={{ min: 3, max: 7 }}
              style={{ marginTop: 10 }}
            />
          </DemoPanel>

          <DemoPanel title="Form.Textarea关闭校验">
            <Textarea
              placeholder="关闭校验"
              isChecking={false}
            />
          </DemoPanel>

          <DemoPanel title="Form水平排布(默认)">
            <Form>
              <FormItem>
                <Label htmlFor required help="#">班级名称</Label>
                <Input
                  name="text-1"
                  type="text"
                  placeholder="请填写班级名称"
                />
              </FormItem>

              <FormItem>
                <Label htmlFor help="#">报名限额</Label>
                <Textarea
                  name="textarea-1"
                  type="text"
                  placeholder="请填写报名限额"
                />
              </FormItem>
            </Form>
          </DemoPanel>

          <DemoPanel title="Form垂直排布horizontal=false">
            <Form horizontal={false}>
              <FormItem>
                <Label htmlFor required help="#">班级名称</Label>
                <Input
                  name="text-2"
                  type="text"
                  placeholder="请填写班级名称"
                />
              </FormItem>

              <FormItem>
                <Label htmlFor help="#">报名限额</Label>
                <Textarea
                  name="textarea-2"
                  type="text"
                  placeholder="请填写报名限额"
                />
              </FormItem>
            </Form>
          </DemoPanel>
        </Demo>
      </div>
    );
  }
}

const demo = document.getElementById('demo');

if (demo) {
  ReactDom.render(<App />, demo);
}
